"use client"

import * as React from "react"
import { AnimatePresence, motion, Variants } from "framer-motion"
import { 
  Bell, Check, Clock, X, Filter, SortDesc, Trash2, 
  CheckSquare, MailOpen, Search, ChevronDown 
} from "lucide-react"
import { useTheme } from "next-themes"

import {
  Avatar,
  AvatarFallback,
  AvatarImage,
} from "@/components/ui/avatar"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import { Skeleton } from "@/components/ui/skeleton"
import { toast } from "@/components/ui/use-toast"

interface Notification {
  id: string
  category: "TASK" | "PROJECT" | "SYSTEM" | "MESSAGE" | "UPDATE"
  type: string
  message: string
  priority: number
  read: boolean
  data?: Record<string, any>
  createdAt: string
}

interface NotificationPanelProps {
  notifications: Notification[]
  loading?: boolean
  onMarkAsRead?: (id: string) => void
  onMarkAllAsRead?: () => void
  onDelete?: (id: string) => void
}

const getCategoryIcon = (category: Notification["category"]) => {
  switch (category) {
    case "TASK":
      return "🎯"
    case "PROJECT":
      return "📂"
    case "SYSTEM":
      return "⚙️"
    case "MESSAGE":
      return "💬"
    case "UPDATE":
      return "📢"
    default:
      return "📌"
  }
}

const getPriorityColor = (priority: number) => {
  if (priority >= 80) return "destructive"
  if (priority >= 60) return "warning"
  if (priority >= 40) return "secondary"
  return "default"
}

const panelVariants: Variants = {
  hidden: { x: "100%", opacity: 0 },
  visible: { 
    x: 0, 
    opacity: 1,
    transition: { 
      type: "spring",
      stiffness: 300,
      damping: 30
    }
  },
  exit: { 
    x: "100%", 
    opacity: 0,
    transition: { 
      type: "spring",
      stiffness: 300,
      damping: 30
    }
  }
}

const notificationVariants: Variants = {
  hidden: { height: 0, opacity: 0 },
  visible: { 
    height: "auto", 
    opacity: 1,
    transition: { 
      type: "spring",
      stiffness: 500,
      damping: 30
    }
  },
  exit: { 
    height: 0, 
    opacity: 0,
    transition: {
      type: "spring",
      stiffness: 500,
      damping: 30
    }
  }
}

export function NotificationPanel({
  notifications,
  loading = false,
  onMarkAsRead,
  onMarkAllAsRead,
  onDelete,
}: NotificationPanelProps) {
  const { theme } = useTheme()
  const [selectedCategory, setSelectedCategory] = React.useState<Notification["category"] | "ALL">("ALL")
  const [searchQuery, setSearchQuery] = React.useState("")
  const [sortBy, setSortBy] = React.useState<"date" | "priority">("date")
  const [showUnreadOnly, setShowUnreadOnly] = React.useState(false)
  const [isExpanded, setIsExpanded] = React.useState(false)

  const filteredNotifications = React.useMemo(() => {
    return notifications
      .filter((n) => {
        // Category filter
        const categoryMatch = selectedCategory === "ALL" || n.category === selectedCategory;
        
        // Search filter
        const searchMatch = n.message.toLowerCase().includes(searchQuery.toLowerCase()) ||
                          n.type.toLowerCase().includes(searchQuery.toLowerCase());
        
        // Unread filter
        const unreadMatch = !showUnreadOnly || !n.read;

        return categoryMatch && searchMatch && unreadMatch;
      })
      .sort((a, b) => {
        if (sortBy === "priority") {
          // Sort by priority first, then by date
          if (a.priority !== b.priority) return b.priority - a.priority;
        }
        // Default sort by date
        return new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime();
      })
  }, [notifications, selectedCategory, searchQuery, showUnreadOnly, sortBy])

  const handleMarkAsRead = (id: string) => {
    onMarkAsRead?.(id)
    toast({
      title: "Notification marked as read",
      description: "The notification has been marked as read.",
    })
  }

  const handleDelete = (id: string) => {
    onDelete?.(id)
    toast({
      title: "Notification deleted",
      description: "The notification has been deleted.",
      variant: "destructive",
    })
  }

  return (
    <motion.div
      initial="hidden"
      animate="visible"
      exit="exit"
      variants={panelVariants}
      className="fixed right-0 top-0 h-screen w-96 bg-background shadow-2xl z-50"
    >
      <Card className="h-full border-none rounded-none">
        <CardHeader className="sticky top-0 z-10 bg-background/95 backdrop-blur-sm border-b pb-4">
          <div className="flex items-center justify-between">
            <CardTitle className="text-xl font-bold">Notifications</CardTitle>
            <div className="flex gap-2">
              <Button 
                variant="outline" 
                size="icon"
                onClick={() => setIsExpanded(!isExpanded)}
              >
                <ChevronDown className={`h-4 w-4 transition-transform ${isExpanded ? 'rotate-180' : ''}`} />
              </Button>
              <Button 
                variant="outline" 
                size="icon"
                onClick={() => onMarkAllAsRead?.()}
              >
                <MailOpen className="h-4 w-4" />
              </Button>
            </div>
          </div>
          <CardDescription className="flex items-center justify-between">
            <span>{notifications.filter((n) => !n.read).length} unread notifications</span>
          </CardDescription>

          {isExpanded && (
            <motion.div
              initial={{ height: 0 }}
              animate={{ height: "auto" }}
              exit={{ height: 0 }}
              className="space-y-4 mt-4"
            >
              {/* Search bar */}
              <div className="relative">
                <Search className="absolute left-2 top-2.5 h-4 w-4 text-muted-foreground" />
                <Input
                  placeholder="Search notifications..."
                  className="pl-8"
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                />
              </div>

              {/* Filters and controls */}
              <div className="flex items-center gap-2">
                <DropdownMenu>
                  <DropdownMenuTrigger asChild>
                    <Button variant="outline" size="sm" className="h-8">
                      <Filter className="h-4 w-4 mr-1" />
                      {selectedCategory}
                    </Button>
                  </DropdownMenuTrigger>
                  <DropdownMenuContent align="end">
                    <DropdownMenuItem onClick={() => setSelectedCategory("ALL")}>
                      All Notifications
                    </DropdownMenuItem>
                    <DropdownMenuSeparator />
                    {["TASK", "PROJECT", "MESSAGE", "SYSTEM", "UPDATE"].map((category) => (
                      <DropdownMenuItem
                        key={category}
                        onClick={() => setSelectedCategory(category as Notification["category"])}
                      >
                        {category.charAt(0) + category.slice(1).toLowerCase()}
                      </DropdownMenuItem>
                    ))}
                  </DropdownMenuContent>
                </DropdownMenu>

                <DropdownMenu>
                  <DropdownMenuTrigger asChild>
                    <Button variant="outline" size="sm" className="h-8">
                      <SortDesc className="h-4 w-4 mr-1" />
                      Sort
                    </Button>
                  </DropdownMenuTrigger>
                  <DropdownMenuContent align="end">
                    <DropdownMenuItem onClick={() => setSortBy("date")}>
                      By Date
                    </DropdownMenuItem>
                    <DropdownMenuItem onClick={() => setSortBy("priority")}>
                      By Priority
                    </DropdownMenuItem>
                  </DropdownMenuContent>
                </DropdownMenu>

                <Button
                  variant={showUnreadOnly ? "secondary" : "outline"}
                  size="sm"
                  className="h-8"
                  onClick={() => setShowUnreadOnly(!showUnreadOnly)}
                >
                  <CheckSquare className="h-4 w-4 mr-1" />
                  Unread
                </Button>
              </div>
            </motion.div>
          )}
        </CardHeader>
      <CardContent className="h-[calc(100vh-180px)] overflow-y-auto space-y-2 p-4">
        {loading ? (
          // Loading skeletons
          Array.from({ length: 3 }).map((_, i) => (
            <div key={i} className="flex items-center space-x-4 rounded-md border p-4">
              <Skeleton className="h-12 w-12 rounded-full" />
              <div className="flex-1 space-y-2">
                <Skeleton className="h-4 w-[250px]" />
                <Skeleton className="h-4 w-[200px]" />
              </div>
            </div>
          ))
        ) : (
          <AnimatePresence initial={false}>
            {filteredNotifications.map((notification) => (
              <motion.div
                key={notification.id}
                layout
                initial="hidden"
                animate="visible"
                exit="exit"
                variants={notificationVariants}
                className={`group relative rounded-lg p-4 transition-colors ${
                  notification.read ? 'bg-background' : 'bg-muted/30'
                } hover:bg-muted/50`}
              >
                <div className="flex items-start gap-4">
                  <Avatar>
                    <AvatarFallback>
                      {getCategoryIcon(notification.category)}
                    </AvatarFallback>
                    {notification.data?.avatarUrl && (
                      <AvatarImage src={notification.data.avatarUrl} />
                    )}
                  </Avatar>
                  <div className="flex-1 space-y-1">
                    <p className={notification.read ? "text-muted-foreground" : "font-medium"}>
                      {notification.message}
                    </p>
                    <div className="flex flex-wrap items-center gap-2">
                      <Badge variant={getPriorityColor(notification.priority)}>
                        {notification.category}
                      </Badge>
                      <span className="text-xs text-muted-foreground flex items-center">
                        <Clock className="mr-1 h-3 w-3" />
                        {new Date(notification.createdAt).toLocaleString()}
                      </span>
                    </div>
                  </div>
                  <div className="flex flex-col gap-2 opacity-0 transition-opacity group-hover:opacity-100">
                    {!notification.read && (
                      <Button
                        variant="ghost"
                        size="icon"
                        className="h-7 w-7"
                        onClick={() => handleMarkAsRead(notification.id)}
                      >
                        <Check className="h-4 w-4" />
                      </Button>
                    )}
                    <Button
                      variant="ghost"
                      size="icon"
                      className="h-7 w-7 text-destructive hover:text-destructive"
                      onClick={() => handleDelete(notification.id)}
                    >
                      <Trash2 className="h-4 w-4" />
                    </Button>
                  </div>
                </div>
                {!notification.read && (
                  <div className="absolute left-0 top-0 h-full w-1 rounded-l-lg bg-primary" />
                )}
              </motion.div>
            ))}
            {filteredNotifications.length === 0 && (
              <motion.div 
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                className="flex h-32 items-center justify-center text-center"
              >
                <div className="text-muted-foreground space-y-2">
                  <Bell className="h-8 w-8 mx-auto opacity-20" />
                  <p>No notifications to display</p>
                </div>
              </motion.div>
            )}
          </AnimatePresence>
        )}
      </CardContent>
    </Card>
    </motion.div>
  )
}