"use client"

import * as React from "react"
import { motion, AnimatePresence } from "framer-motion"
import { cn } from "@/lib/utils"
import {
  Sidebar,
  SidebarProps,
  SidebarProvider,
  useSidebar,
  SidebarContext
} from "@/components/ui/sidebar"

interface AnimatedSidebarProps extends Omit<SidebarProps, 'onCollapseChange'> {
  className?: string;
}

export function AnimatedSidebar({ className, children, ...props }: AnimatedSidebarProps) {
  const sidebarVariants = {
    expanded: {
      width: "var(--sidebar-width)",
      transition: { duration: 0.3, ease: [0.4, 0, 0.2, 1] }
    },
    collapsed: {
      width: "var(--sidebar-width-icon)",
      transition: { duration: 0.3, ease: [0.4, 0, 0.2, 1] }
    }
  }

  const contentVariants = {
    expanded: {
      opacity: 1,
      x: 0,
      transition: { 
        duration: 0.2,
        delay: 0.1,
        ease: [0.4, 0, 0.2, 1]
      }
    },
    collapsed: {
      opacity: 0,
      x: -10,
      transition: { 
        duration: 0.2,
        ease: [0.4, 0, 0.2, 1]
      }
    }
  }

  return (
    <SidebarProvider>
      <div className={cn("relative overflow-hidden transition-all duration-300", className)}>
        <SidebarContext.Consumer>
          {(context) => {
            if (!context) return null;
            return (
              <motion.div
                initial={false}
                animate={context.state === "expanded" ? "expanded" : "collapsed"}
                variants={sidebarVariants}
                className="h-full"
              >
                <Sidebar {...props} className="h-full">
                  <AnimatePresence mode="wait">
                    <motion.div
                      key={context.state}
                      initial="collapsed"
                      animate="expanded"
                      exit="collapsed"
                      variants={contentVariants}
                      className="h-full"
                    >
                      {children}
                    </motion.div>
                  </AnimatePresence>
                </Sidebar>
              </motion.div>
            );
          }}
        </SidebarContext.Consumer>
      </div>
    </SidebarProvider>
  )
}