"use client";

import { useEffect } from "react";
import { WifiOff, Wifi } from "lucide-react";
import {
  Tooltip,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from "@/components/ui/tooltip";
import { useConnectionStore } from "@/store/connection-store";

export function ConnectionStatus() {
  const { isConnected, isChecking, checkConnection, lastChecked } = useConnectionStore();

  useEffect(() => {
    // Initial check
    checkConnection();

    // Set up periodic checks every 30 seconds
    const interval = setInterval(() => {
      checkConnection();
    }, 30000);

    return () => clearInterval(interval);
  }, [checkConnection]);

  const getStatusText = () => {
    if (isChecking) return "Checking connection...";
    if (isConnected) return "Connected to backend";
    return "Backend disconnected";
  };

  const getLastCheckedText = () => {
    if (!lastChecked) return "";
    const timeAgo = Math.floor((Date.now() - lastChecked.getTime()) / 1000);
    if (timeAgo < 60) return `Last checked ${timeAgo} seconds ago`;
    return `Last checked ${Math.floor(timeAgo / 60)} minutes ago`;
  };

  return (
    <TooltipProvider>
      <Tooltip>
        <TooltipTrigger asChild>
          <button
            onClick={() => checkConnection()}
            className={`p-2 rounded-full transition-colors ${
              isConnected
                ? "text-green-500 hover:bg-green-500/10"
                : "text-red-500 hover:bg-red-500/10"
            }`}
          >
            {isConnected ? (
              <Wifi className="h-4 w-4" />
            ) : (
              <WifiOff className="h-4 w-4" />
            )}
          </button>
        </TooltipTrigger>
        <TooltipContent>
          <p>{getStatusText()}</p>
          <p className="text-xs text-muted-foreground">{getLastCheckedText()}</p>
        </TooltipContent>
      </Tooltip>
    </TooltipProvider>
  );
}